.App {
  background: -webkit-linear-gradient(bottom, #189AB4, #75E6DA);
  background-repeat:no-repeat;
  max-width: 100vw;
  height:100%;
  min-height: 100vh;
  background-attachment:scroll;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

